import foot from '@/components/aashare/foot';
import api from '@/api';
export default {
  name: 'hello',
  data () {
    return {
       list:"",
       page:1
    }
  },
  created:function(){
       api.ajax('',
         "Article/AboutUs",{}
        ).then(res=>{
          console.log(res);
          this.list = res.data.result.articles;
        }).catch(()=>{
          console.log("");
        });
   },
  methods: {
     goback(){
       window.history.go(-1);
     },
     goDetail(id){
         this.$router.push({path:"/activeDetail",query:{id:id}});
     }
  },
   components: {
      foot
  }
  
}



